export const hundleCartFetchData = async () => {
	const response = await fetch("https://checkout.free.beeceptor.com/items", {
		method: "GET",
	});

	return await response.json();
};

export const hundleShippingFetchData = async () => {
	const response = await fetch("https://checkout.free.beeceptor.com/shipping", {
		method: "GET",
	});
	return await response.json();
};

export const hundleCouponsFetchData = async () => {
	const response = await fetch("https://checkout.free.beeceptor.com/coupons", {
		method: "GET",
	});
	return await response.json();
};

export const hundleTotalPriceFetchData = async (
	isCouponApply = false,
	shippingType = "dhl",
) => {
	const response = await fetch(
		`https://checkout.free.beeceptor.com/totals?coupon=${isCouponApply}&shipping=${shippingType}`,
		{ method: "GET" },
	);
	return await response.json();
};

export const hundleSubmitFetchData = async () => {
	const response = await fetch("https://checkout.free.beeceptor.com/submit", {
		method: "POST",
	});
	return await response.json();
};
