import { Component, State, h } from "@stencil/core";

@Component({
	tag: "checkout-layout",
	styleUrl: "checkout-layout.css",
	shadow: true,
})
export class CheckoutLayout {
	@State() path: string = location.pathname;

	componentWillLoad() {
		addEventListener("locationChange", () => {
			this.path = location.pathname;
		});
		addEventListener("popstate", () => {
			this.path = location.pathname;
		});
	}

	render() {
		const { path } = this;
		if (path === "/")
			return (
				<div id="container">
					<top-header-layout />
					<progress-indicator />
					<cart-layout />
				</div>
			);
		if (path === "/shipping")
			return (
				<div id="container">
					<top-header-layout />
					<progress-indicator />
					<shipping-layout />
				</div>
			);
		if (path === "/checkout")
			return (
				<div id="container">
					<top-header-layout />
					<progress-indicator />
					<total-price-layout />
				</div>
			);
		if (path === "/success")
			return (
				<div id="container">
					<success-layout />
				</div>
			);
		return (
			<div id="container">
				<top-header-layout />
				<page-not-found-layout />
			</div>
		);
	}
}
