import { Component, Prop, State, Watch, getAssetPath, h } from "@stencil/core";
import type {
	checkoutData,
	checkoutItem,
	couponsData,
	couponsItem,
} from "../../utils/types/globalTyples";
import state from "../../utils/store";
import { navigate } from "../../utils/utils";
import {
	hundleCouponsFetchData,
	hundleSubmitFetchData,
	hundleTotalPriceFetchData,
} from "../../utils/apis";

@Component({
	tag: "total-price-layout",
	styleUrl: "total-price-layout.css",
	shadow: true,
	assetsDirs: ["assets"],
})
export class TotalPrice {
	@Prop() shippingType: string;
	@State() couponstData: couponsData = { data: [] };
	@State() couponText = "";
	@State() couponErrorMsg = "";
	@State() selectedCoupon: couponsItem;
	@State() isCouponApply = false;
	@State() extractCouponPrice: checkoutItem;
	@State() discountedPercintage = 0;
	@State() discountedAmount = 0;
	@State() extractCartTotal: checkoutItem;
	@State() extractTotal: checkoutItem;
	@State() checkoutData: checkoutData = { data: [] };
	@State() extractShippingFee: checkoutItem;
	@State() isSubmitFetching = false;
	@State() isCouponFetching = false;
	@State() isCheckoutFetching = false;

	@Watch("checkoutData")
	watchCheckoutData(newValue: boolean, oldValue: boolean) {
		if (oldValue !== newValue) {
			this.extractCartTotal = this.checkoutData.data.find(
				(i) => i.name === "cart_total",
			);
			this.extractShippingFee = this.checkoutData.data.find(
				(i) => i.name === "shipping_fees",
			);
			this.extractCouponPrice = this.checkoutData.data.find(
				(i) => i.name === "discount",
			);
			this.extractTotal = this.checkoutData.data.find(
				(i) => i.name === "total",
			);
		}
	}

	@Watch("isCouponApply")
	watchIsCouponApply(newValue: boolean, oldValue: boolean) {
		if (oldValue !== newValue) {
			this.hundlePriceAPI(this.isCouponApply, state.shippingType);
			this.couponstData.data.find((i) => {
				if (i.label === this.couponText) {
					this.discountedPercintage = Number(i.discount.amount) / 100;
				}
			});
		}
	}

	@Watch("discountedPercintage")
	watchdDiscountedAmount(newValue: boolean, oldValue: boolean) {
		if (oldValue !== newValue) {
			const formatter = new Intl.NumberFormat("en-US");
			const _discountedAmount =
				this.discountedPercintage * this.extractCartTotal.amount;
			const formattedAmount = formatter.format(_discountedAmount);
			this.discountedAmount = Number(formattedAmount);
		}
	}
	@Watch("shippingType")
	watchShippingType(newValue: boolean, oldValue: boolean) {
		if (oldValue !== newValue) {
			this.hundlePriceAPI(this.isCouponApply, this.shippingType);
			state.shippingType = this.shippingType;
		}
	}

	componentWillLoad() {
		if (location.pathname.includes("checkout")) {
			state.progressIndicator = "checkout";
			state.buttonText = "Submit";
		}
		this.hundleCouponAPI();
		this.hundlePriceAPI(this.isCouponApply, state.shippingType);
	}

	async hundlePriceAPI(isCouponApply = false, shippingType = "dhl") {
		try {
			this.isCheckoutFetching = true;
			const priceResponse = await hundleTotalPriceFetchData(
				isCouponApply,
				shippingType,
			);
			if (priceResponse) {
				this.checkoutData = priceResponse || [];
				this.isCheckoutFetching = false;
			}
		} catch (error) {
			this.isCheckoutFetching = false;
		}
	}

	async hundleCouponAPI() {
		try {
			this.isCouponFetching = true;
			const couponResponse = await hundleCouponsFetchData();
			if (couponResponse) {
				this.couponstData = couponResponse || [];
				this.isCouponFetching = false;
			}
		} catch (error) {
			this.isCouponFetching = false;
		}
	}

	async hundleSubmitCheckoutAPI() {
		try {
			this.isSubmitFetching = true;
			const submitResponse = await hundleSubmitFetchData();
			if (submitResponse) {
				state.submit.APIData.title = submitResponse.data.title;
				state.submit.APIData.subtitle = submitResponse.data.subtitle;
				this.isSubmitFetching = false;
				navigate("success");
			}
		} catch (error) {
			this.isSubmitFetching = false;
		}
	}

	hundleSubmit(ev: MouseEvent): void {
		ev.preventDefault();
		switch (state.progressIndicator) {
			case "cart":
				navigate("shipping");
				break;
			case "shipping":
				navigate("checkout");
				break;
			case "checkout":
				this.hundleSubmitCheckoutAPI();
				break;
			default:
				navigate("shipping");
				break;
		}
	}

	handleCouponOnChange(ev): void {
		this.couponText = ev.target.value;
		if (!ev.target.value) {
			this.couponErrorMsg = "";
		}
	}

	hundleApplyCoupon(ev: MouseEvent): void {
		ev.preventDefault();
		console.log(this.extractCouponPrice.label);
		if (!this.couponText) {
			return;
		}
		if (this.couponstData.data.find((i) => i.label === this.couponText)) {
			this.isCouponApply = true;
			this.couponErrorMsg = "";
		} else {
			this.couponErrorMsg = "Coupon not valid or expired";
		}
	}

	hundleDeleteCoupon(ev: MouseEvent): void {
		ev.preventDefault();
		this.isCouponApply = false;
		this.couponText = "";
	}

	renderPriceSection(item: checkoutItem) {
		const formatter = new Intl.NumberFormat("en-US");
		const formattedAmount = formatter.format(item?.amount);
		return (
			<div class="flex mb-2" key={item?.name}>
				<p class="flex flex-1 text-sm font-bold text-teal-800">{item?.label}</p>
				<p class="flex flex-1  justify-end text-sm font-bold text-teal-800">
					{item?.currency} {formattedAmount}
				</p>
			</div>
		);
	}

	renderCouponSection() {
		const { isCouponApply, couponText, couponErrorMsg } = this;
		if (isCouponApply && couponText) {
			return (
				<div class="flex mb-2">
					<div class="flex flex-1 text-sm font-bold text-teal-800">
						<div class="relative">
							<img
								class="absolute right-2 top-3"
								src={getAssetPath("assets/delete.png")}
								alt={"delete"}
							/>
							<img
								class="absolute bottom-1/3 left-2"
								src={getAssetPath("assets/coupon.png")}
								alt={"coupon"}
							/>
							<button
								class="flex static h-9 pr-6 pl-6 border-2 border-stone-200 rounded-md text-xs bg-white text-stone-20 justify-center items-center"
								type="submit"
								onClick={(ev) => this.hundleDeleteCoupon(ev)}
							>
								{couponText}
							</button>
						</div>
					</div>

					<p class="flex flex-1 text-sm font-bold justify-end text-red-600">
						SAR - {this.discountedAmount}
					</p>
				</div>
			);
		}

		return (
			<div class="flex mb-2 mt-2">
				<p class="text-xs font-bold text-teal-800">Have a coupon?</p>
				<div class="flex flex-1 felx-col text-sm font-bold justify-end">
					<div class="relative">
						<input
							class="static p-2 h-9 rounded-md text-xs border-stone-200 text-stone-20 justify-center items-center"
							type="text"
							placeholder="insert code"
							onInput={(event) => this.handleCouponOnChange(event)}
							value={couponText}
						/>
						<button
							class="absolute right-2 top-1 p-2 h-7 rounded-md text-xs text-white bg-teal-800 hover:bg-teal-700 justify-center items-center self-center"
							type="submit"
							onClick={(ev) => this.hundleApplyCoupon(ev)}
						>
							Apply
						</button>
						<p class="text-xs text-yellow-600">{couponErrorMsg}</p>
					</div>
				</div>
			</div>
		);
	}

	render() {
		const { progressIndicator } = state;
		if (this.isCouponFetching && this.isCheckoutFetching) {
			return (
				<div>
					<div class="flex mt-6 flex-col h-16 pt-4 pb-4 rounded-2xl bg-slate-100" />
					<div class="flex mt-2 flex-col h-4 pt-4 pb-4 rounded-2xl bg-slate-100" />
				</div>
			);
		}

		return (
			<div class="flex flex-col mt-6">
				{this.extractCartTotal
					? this.renderPriceSection(this.extractCartTotal)
					: null}
				{(this.extractShippingFee && progressIndicator === "shipping") ||
				progressIndicator === "checkout"
					? this.renderPriceSection(this.extractShippingFee)
					: null}
				{this.extractTotal &&
				this.extractCouponPrice &&
				progressIndicator === "checkout" ? (
					<div>
						{this.renderCouponSection()}
						{this.renderPriceSection(this.extractTotal)}
					</div>
				) : null}
				{this.isSubmitFetching ? (
					<div role="status">
						<svg
							aria-hidden="true"
							class="w-4 h-4 text-gray-200 animate-spin  fill-teal-800"
							viewBox="0 0 100 101"
							fill="none"
							xmlns="http://www.w3.org/2000/svg"
						>
							<path
								d="M100 50.5908C100 78.2051 77.6142 100.591 50 100.591C22.3858 100.591 0 78.2051 0 50.5908C0 22.9766 22.3858 0.59082 50 0.59082C77.6142 0.59082 100 22.9766 100 50.5908ZM9.08144 50.5908C9.08144 73.1895 27.4013 91.5094 50 91.5094C72.5987 91.5094 90.9186 73.1895 90.9186 50.5908C90.9186 27.9921 72.5987 9.67226 50 9.67226C27.4013 9.67226 9.08144 27.9921 9.08144 50.5908Z"
								fill="currentColor"
							/>
							<path
								d="M93.9676 39.0409C96.393 38.4038 97.8624 35.9116 97.0079 33.5539C95.2932 28.8227 92.871 24.3692 89.8167 20.348C85.8452 15.1192 80.8826 10.7238 75.2124 7.41289C69.5422 4.10194 63.2754 1.94025 56.7698 1.05124C51.7666 0.367541 46.6976 0.446843 41.7345 1.27873C39.2613 1.69328 37.813 4.19778 38.4501 6.62326C39.0873 9.04874 41.5694 10.4717 44.0505 10.1071C47.8511 9.54855 51.7191 9.52689 55.5402 10.0491C60.8642 10.7766 65.9928 12.5457 70.6331 15.2552C75.2735 17.9648 79.3347 21.5619 82.5849 25.841C84.9175 28.9121 86.7997 32.2913 88.1811 35.8758C89.083 38.2158 91.5421 39.6781 93.9676 39.0409Z"
								fill="currentFill"
							/>
						</svg>
					</div>
				) : null}
				<button
					class="flex mt-6 p-4 rounded-md text-white bg-teal-800 hover:bg-teal-700 justify-center"
					type="submit"
					onClick={(ev) => this.hundleSubmit(ev)}
				>
					{state.buttonText}
				</button>
			</div>
		);
	}
}
