import { Component, h, State } from "@stencil/core";
import type {
	ShippingData,
	shippingItem,
} from "../../utils/types/globalTyples";
import state from "../../utils/store";
import { hundleShippingFetchData } from "../../utils/apis";

@Component({
	tag: "shipping-layout",
	styleUrl: "shipping-layout.css",
	shadow: true,
})
export class ShippingLayout {
	@State() shippingtData: ShippingData = { data: [] };
	@State() isShippingLoading = false;

	componentWillLoad() {
		state.progressIndicator = "shipping";
		state.buttonText = "Proceed to Payment";
		this.hundleShippingAPI();
	}

	async hundleShippingAPI() {
		try {
			this.isShippingLoading = true;
			const response = await hundleShippingFetchData();
			if (response) {
				this.shippingtData = response || [];
				this.isShippingLoading = false;
			}
		} catch (error) {
			this.isShippingLoading = false;
		}
	}

	hundleShippingSelect(ev: MouseEvent, item: shippingItem) {
		ev.preventDefault();
		state.shippingType = item.name;
	}

	render() {
		if (this.isShippingLoading) {
			return (
				<div class="flex flex-col h-64  pt-4 pb-4 rounded-2xl bg-slate-100" />
			);
		}
		return (
			<div>
				{this.shippingtData.data.map((item) => {
					return (
						<div
							key={item.id}
							class={`flex mt-4 border-solid border-2 pt-4 pb-4 rounded-2xl  
							${
								item.name === state.shippingType
									? "border-teal-100"
									: "border-white"
							}
							${item.name === state.shippingType ? "bg-teal-50" : "bg-white"} `}
						>
							<input
								id={item.id}
								name="check"
								type="radio"
								value={state.shippingType}
								onClick={(ev) => this.hundleShippingSelect(ev, item)}
								class="ml-3 mr-3 appearance-none w-4 h-4 border-1 border-stone-400 rounded-full"
							/>
							<img src={item.logo} alt={item.label} width="70" height="40" />
							<label htmlFor={item.id} class="ml-3 mr-3 text-sm text-gray-700">
								{item.label} {item.fees.currency} + {item.fees.amount}
							</label>
						</div>
					);
				})}
				<total-price-layout shippingType={state.shippingType} />
			</div>
		);
	}
}
