import { Component, getAssetPath, h } from "@stencil/core";
import { navigate } from "../../utils/utils";
import state, { reset } from "../../utils/store";

@Component({
	tag: "success-layout",
	styleUrl: "success-layout.css",
	shadow: true,
	assetsDirs: ["assets"],
})
export class SuccessLayout {
	return(ev: MouseEvent): void {
		ev.preventDefault();
		if (location.pathname.includes("success")) {
			reset();
			navigate("/");
		}
	}

	render() {
		const { title, subtitle } = state.submit.APIData;
		return (
			<div class="flex flex-col h-screen justify-center items-center gap-2">
				<div class="flex flex-col w-9 items-center justify-center mr-2">
					<img src={getAssetPath("assets/logo-top.png")} alt={"logo"} />
					<img src={getAssetPath("assets/logo.png")} alt={"logo"} />
				</div>
				<p class="gap-0 p-0 m-0 text-xl font-bold text-teal-800">
					Majid Aljishi Store
				</p>
				<p class="text-3xl font-bold text-teal-800">{title}</p>
				<p class="text-l  text-teal-800">{subtitle}</p>
				<button
					class="text-sm text-slate-400 underline"
					type="submit"
					onClick={(ev) => this.return(ev)}
				>
					return to the store
				</button>
			</div>
		);
	}
}
