import { Component, State, h } from "@stencil/core";
import type { CartData } from "../../utils/types/globalTyples";
import { hundleCartFetchData } from "../../utils/apis";

@Component({
	tag: "cart-layout",
	styleUrl: "cart-layout.css",
	shadow: true,
})
export class CartLayout {
	@State() cartData: CartData = { data: [] };
	@State() isFetching = false;

	componentWillLoad() {
		this.hundleCartAPI();
	}

	async hundleCartAPI() {
		try {
			this.isFetching = true;
			const response = await hundleCartFetchData();
			if (response) {
				this.cartData = response || [];
				this.isFetching = false;
			}
		} catch (error) {
			this.isFetching = false;
		}
	}

	render() {
		if (this.isFetching) {
			return (
				<div class="flex flex-col h-64  pt-4 pb-4 rounded-2xl bg-slate-100" />
			);
		}
		return (
			<div class="flex flex-col">
				<div class="flex flex-col border-solid border-2 pt-4 pb-4 rounded-2xl border-teal-100 bg-teal-50">
					{this.cartData.data.map((item) => {
						return (
							<div key={item.id} class="flex">
								<div class="flex-none justify-center items-center">
									<img
										class="rounded-full m-4 ring-1 ring-slate-500"
										src={item.thumbnail}
										alt={item.label}
										width="50"
										height="50"
									/>
								</div>
								<div class="flex basis-1/2 flex-col justify-center items-start">
									<p class="text-sm text-teal-800 underline">{item.label}</p>
									<p class="text-xs text-stone-400">
										{item.price.currency} {item.price.amount}
									</p>
								</div>
								<div class="flex flex-1 justify-end items-center">
									<div class="flex flex-1 justify-center items-center">
										<p class="font-bold text-xs">{item.qty}</p>
									</div>
									<div class="flex flex-grow justify-end items-center pr-4 ">
										<p class="font-bold text-xs pr-1">{item.price.currency}</p>
										<p class="font-bold text-xs">{item.price.amount}</p>
									</div>
								</div>
							</div>
						);
					})}
				</div>
				<total-price-layout />
			</div>
		);
	}
}
