import { Component, State, h } from "@stencil/core";
import state from "../../utils/store";

@Component({
	tag: "progress-indicator",
	styleUrl: "progress-indicator.css",
	shadow: true,
})
export class ProgressIndicator {
	@State() colorTealLight = "text-slate-300";
	@State() colorTealDark = "text-teal-800";
	@State() bgcolorTealLight = "bg-slate-300";
	@State() bgcolorTealDark = "bg-teal-800";
	render() {
		const { progressIndicator } = state;

		if (progressIndicator === "cart") {
			return (
				<div id="container">
					<div class="flex flex-row flex-1">
						<p class={`shrink text-sm font-bold ${this.colorTealDark}`}>Cart</p>
						<div
							class={`flex-grow h-0.5 self-center ml-2 mr-2 ${this.bgcolorTealDark}`}
						/>
						<p class={`shrink text-sm font-bold ${this.colorTealLight}`}>
							Shipping
						</p>
						<div
							class={`flex-grow h-0.5 self-center ml-2 mr-2 ${this.bgcolorTealLight}`}
						/>
						<p class={`shrink text-sm font-bold ${this.colorTealLight}`}>
							Checkout
						</p>
					</div>
				</div>
			);
		}

		if (progressIndicator === "shipping") {
			return (
				<div id="container">
					<div class="flex flex-row flex-1">
						<p class={`shrink text-sm font-bold ${this.colorTealDark}`}>Cart</p>
						<div
							class={`flex-grow h-0.5 self-center ml-2 mr-2 ${this.bgcolorTealDark}`}
						/>
						<p class={`shrink text-sm font-bold ${this.colorTealDark}`}>
							Shipping
						</p>
						<div
							class={`flex-grow h-0.5 self-center ml-2 mr-2 ${this.bgcolorTealDark}`}
						/>
						<p class={`shrink text-sm font-bold ${this.colorTealLight}`}>
							Checkout
						</p>
					</div>
				</div>
			);
		}

		return (
			<div id="container">
				<div class="flex flex-row flex-1">
					<p class={`shrink text-sm font-bold ${this.colorTealDark}`}>Cart</p>
					<div
						class={`flex-grow h-0.5 self-center ml-2 mr-2 ${this.bgcolorTealDark}`}
					/>
					<p class={`shrink text-sm font-bold ${this.colorTealDark}`}>
						Shipping
					</p>
					<div
						class={`flex-grow h-0.5 self-center ml-2 mr-2 ${this.bgcolorTealDark}`}
					/>
					<p class={`shrink text-sm font-bold ${this.colorTealDark}`}>
						Checkout
					</p>
				</div>
			</div>
		);
	}
}
